.main-modal-calendar-schedule {
	.modal-content {
		border-width: 0;
		position: relative;
		>.close {
			position: absolute;
			top: 20px;
			right: 25px;
			font-size: 28px;
			font-weight: 700;
			color: $gray-600;
			text-shadow: none;
		}
	}
	.modal-header {
		padding: 20px 25px;
	}
	.modal-body {
		padding: 30px 25px;
	}
	.form-group-date {
		display: flex;
		align-items: center;
		border: 1px solid $border;
		background-color: $gray-100;
		padding: 10px 5px;
		color: $gray-900;
		font-size: 13px;
		font-weight: 600;
		>i {
			font-size: 32px;
			line-height: 0;
			margin-right: 10px;
			opacity: .5;
		}
	}
	.select2-container {
		width: 100% !important;
	}
}
.main-modal-calendar-event {
	.modal-content {
		border-width: 0;
		position: relative;
		background-color: transparent;
		>.close {
			font-size: 18px;
			text-shadow: none;
		}
	}
	.modal-header {
		position: relative;
		padding: 20px;
		border-bottom-width: 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
	}
	.nav-modal-event {
		align-items: center;
		.nav-link {
			padding: 0;
			color: rgba(255, 255, 255, 0.75);
			font-size: 21px;
			line-height: 0;
			position: relative;
			transition: all 0.2s ease-in-out;
			&:hover, &:focus {
				color: $white;
			}
			+.nav-link {
				margin-left: 15px;
			}
			&:last-child {
				font-size: 24px;
			}
		}
	}
	.event-title {
		color: $white;
		font-size: 18px;
		font-weight: 500;
		margin-bottom: 0;
		line-height: 1;
	}
	.modal-body {
		padding: 20px;
		background-color: $white;
	}
	.event-start-date, .event-end-date {
		color: $gray-900;
		font-weight: 500;
	}
	.event-desc {
		margin-top: 5px;
		&:empty {
			margin-bottom: 5px;
		}
	}
}
@media (min-width: 576px) {
	.main-modal-calendar-event .modal-header {
		padding: 20px 25px;
	}
}
@media (prefers-reduced-motion: reduce) {
	.main-modal-calendar-event .nav-modal-event .nav-link {
		transition: none;
	}
}
@media (min-width: 576px) {
	.main-modal-calendar-event .modal-body {
		padding: 30px;
	}
}
@media (max-width: 605px) {
	.main-calendar .fc-header-toolbar {
		padding: 0;
	}
	.fc-view {
		border: 1px solid $border;
		>table {
			border: 1px solid $border;
		}
	}
	.card--calendar .ui-datepicker .ui-datepicker-calendar td a {
		width: 28px;
		height: 28px;
	}
	.main-content-body.main-content-body-calendar.card.p-4 {
		padding: 1rem !important;
	}
}
@media (max-width: 320px) {
	.main-calendar .fc-header-toolbar {
		.fc-right {
			flex: 0 0 100%;
			display: flex;
			justify-content: left;
			margin-top: 20px;
		}
		.fc-left {
			flex: 0 0 100%;
			display: flex;
			justify-content: left;
		}
	}
}